.list {
  text-align: center;
  margin: 20px;
  padding: 10px;
}
@media all and (min-width: 768px) {
  .list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0;
  }
}

@media all and (min-width: 1020px) {
  .list {
    display: grid;
    // grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-columns: repeat(auto-fit, 300px);
    grid-gap: 10px;
    padding: 0;
  }
}
