.form {
  text-align: center;
  margin: 0 0 30px 0;

  &--input {
    width: 300px;
    height: 30px;
    padding: 5px;
    box-shadow: -1px -6px 60px 0px rgba(136, 214, 47, 1);
    background-color: #f1c7e0;
    font-size: 16px;
    color: black;
    border: none;
  }
}
@media all and (max-width: 360px) {
  .form {
    margin-bottom: 0;
    &--input {
      height: 35px;
    }
  }
}
